@font-face {
    font-family: 'Poppins';
    src: url('./assets/fonts/poppins/Poppins-Light.woff2') format('woff2');
    font-weight: 300;
}

@font-face {
    font-family: 'Poppins';
    src: url('./assets/fonts/poppins/Poppins-Regular.woff2') format('woff2');
}

@font-face {
    font-family: 'Poppins';
    src: url('./assets/fonts/poppins/Poppins-Italic.woff2') format('woff2');
    font-style: italic;
}

@font-face {
    font-family: 'Poppins';
    src: url('./assets/fonts/poppins/Poppins-Medium.woff2') format('woff2');
    font-weight: 500;
}

@font-face {
    font-family: 'Poppins';
    src: url('./assets/fonts/poppins/Poppins-Semibold.woff2') format('woff2');
    font-weight: 600;
}

@font-face {
    font-family: 'Poppins';
    src: url('./assets/fonts/poppins/Poppins-Bold.woff2') format('woff2');
    font-weight: 700;
}

@font-face {
    font-family: 'Poppins';
    src: url('./assets/fonts/poppins/Poppins-ExtraBold.woff2') format('woff2');
    font-weight: 800;
}

body {
    margin: 0;
    /*font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
        'Droid Sans', 'Helvetica Neue', sans-serif;*/
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

html {
    font-size: 14px;
    overflow-x: hidden;
}

@media screen and (max-width: 400px), screen and (max-height: 750px) {
    html {
        font-size: 12px;
    }
}

@media screen and (max-height: 650px) {
    html {
        font-size: 10px;
    }
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

/* EMBLA */
.embla__selected-snap-display {
    justify-self: flex-end;
    align-self: center;
    /*color: var(--text-low-contrast);*/
    font-weight: 600;
}
